export default {
	components: {},
	data() {
		return {
			headerNav: [{ name: '计算机' }, { name: '驾驶员' }, { name: '汽车修理' }, { name: '计算机' }],
			headerList: [
				{ name: '计算机' },
				{ name: '驾驶员' },
				{ name: '汽车修理' },
				{ name: '计算机' },
				{ name: '计算机' },
				{ name: '驾驶员' },
				{ name: '汽车修理' },
				{ name: '计算机' }
			],
			classList: [
				{
					title: '计算机职业技能',
					id: '001',
					time: '40分钟',
					detail: '本专业是计算机客场详情课程详情本专业是计算机客场详情课程详情',
					price: '300.00'
				},
				{
					title: '计算机职业技能',
					id: '002',
					time: '40分钟',
					detail: '本专业是计算机客场详情课程详情本专业是计算机客场详情课程详情',
					price: '2100.00'
				}
			],
			isshowMore: false,
			navNum: 0,
			moreNum: 0
		};
	},
	onLoad() {},
	methods: {
		showheaderList(e) {
			var that = this;
			that.isshowMore = !that.isshowMore;
		},
		switchNav(e) {
			console.log(this.headerNav[e.currentTarget.dataset.index].name);
			let index = e.currentTarget.dataset.index;
			this.navNum = index;
			this.moreNum = index;
		},
		moreChoose(e) {
			let id = e.currentTarget.dataset.id;
			this.moreNum = id;
			this.navNum = id;
			this.isshowMore = false;
		},
		goBuy(e) {
			console.log('立即购买');
			uni.navigateTo({
				url: 'videoDetail/videoDetail'
			});
		},
		toClassDetail(e) {
			let index = e.currentTarget.dataset.index;
			uni.navigateTo({
				url: 'videoDetail/videoDetail?id=' + this.classList[index].id
			});
		}
	}
};
